<template>
	<div class="order_list">
    <van-nav-bar
      title="提现记录"
      left-text="返回"
      right-text=""
      left-arrow
      @click-left="goBack"
    />
		<van-list
		  	v-model="loading"
		  	:finished="finished"
				:immediate-check="false"
        :offset="10"
		  	@load="loadMore"
		>
			<van-panel
				v-for="(el, i) in items"
				class="order_list--panel"
				:key="i"
        :desc="el.status_text"
				:title="'- ￥' + el.money"
				:status="el.create_time">
			</van-panel>
		</van-list>
		<is-empty v-if="isEmpty">暂无提现记录</is-empty>
	</div>
</template>

<script>
	import { WITHDRAW_LIST } from '@/api/user';

	import { Tab, Tabs, Panel, Card } from 'vant';
	import IsEmpty from "@/vue/components/is-empty/";

	import loadMore from '@/vue/mixin/load-more';
	import scrollFixed from '@/vue/mixin/scroll-fixed';

	export default {
		name: 'withdraw-list',

		mixins: [loadMore, scrollFixed],

		data() {
			return {
				loading: false,
				finished: false,
				activeIndex: 0,
        isEmpty: false,
				items: []
			}
		},

		watch: {
			'$route': 'resetInit'
		},

		created() {
			this.resetInit();
		},

		methods: {
			initData() {
				return this.$reqGet(WITHDRAW_LIST, {
					'per_page': this.pages.perPage,
					page: this.pages.currPage
				}, {
					hideLoading: true
				}).then(res => {
					return this.loadSuccess(res)
				}).catch(err => {
        })
			},
      loadSuccess(res){
				this.loading = false;
				this.finished = true;
        this.isEmpty = false;
        let list = res.data.data.list;
        if(!list){
          setTimeout(() => {
						this.isEmpty = true
					}, 600)
          return this.pages;
        }
        let page = {
          "pageCount":res.data.data.pageCount,
          "currPage":res.data.data.currPage,
          "perPage":res.data.data.perPage,
          "totalCount":res.data.data.totalCount
        };
        this.items.push(...list);
        return page;
      },
			getStatusText(status){
				return STATUS_TEXT[status] || '';
      },
      //返回
      goBack() {
        this.$router.push({path: '/user'});
      }
		},
		components: {
			[Tab.name]: Tab,
			[Tabs.name]: Tabs,
			[Panel.name]: Panel,
			[Card.name]: Card,
			[IsEmpty.name]: IsEmpty,
		}
	}

</script>

<style lang="scss" scoped>
	.order_list {
		padding-bottom: 0;

		&--footer_btn {
			text-align: right;
		}
		&--panel{
			margin-bottom: 3px;
		}

		&--van-card{
			background-color: #fafafa;
		}

		&--total{
			text-align: right;
			padding: 10px;
		}
	}

</style>



